import React from "react";
import { Card, CardHeader, CardBody, CardFooter} from 'reactstrap';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPaperPlane } from '@fortawesome/free-solid-svg-icons'

// RCE CSS
import 'react-chat-elements/dist/main.css';
// MessageBox component
import { MessageList, Input, Button } from 'react-chat-elements';

import {serverUrl} from '../api-config';

import axios from 'axios';


class ChatBox extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            message: '',
            messages: []
        };
        this.handleInputChange  = this.handleInputChange.bind(this);
        this.handleEnter        = this.handleEnter.bind(this);
        this.handleSendClick    = this.handleSendClick.bind(this);
    }
    componentDidMount() {
        this.getMessages();
    }
    handleInputChange(event) {
        const target = event.target;
        const value = target.value;

        this.setState({
            message: value,
        });
    }
    handleEnter(event) {
        if (event.key === 'Enter') {
            this.sendMessage();
        }
    }
    handleSendClick(){
        this.sendMessage();
    }
    sendMessage(){
        const url = serverUrl + '/message/new';
        axios.post(url, {
            text: this.state.message,
            receiverEmail: this.props.receiverEmail,
            saleId: this.props.saleId
        },{
            headers:{
                authorization: this.props.userToken
            }
        }).then((response) => {
            console.log(response.data);
            this.setState({
                message: ''
            });
            this.refs.message.clear();
            this.getMessages();
        }).catch((error) => {
            throw error;
        });
    }
    getMessages(){
        console.log("Sale ID: "+this.props.saleId);
        console.log("Token: "+this.props.userToken);
        const url = serverUrl + '/messages/' + this.props.saleId;
        axios.post(url, {}, {
            headers:{
                authorization: this.props.userToken
            }
        }).then((response) => {
            this.setState({
                messages: response.data.messages
            });
        }).catch((error) => {
            console.log(error);
        });
    }

    render() {
        return (
            <div>
                <Card>
                    <CardHeader className="bg-dark text-white">
                        {this.props.buyer}
                        <span className="float-right"> {this.props.salePrice}₺ </span>
                    </CardHeader>
                    <CardBody style={{minHeight: '400px'}}>
                        <MessageList
                            className='message-list'
                            lockable={true}
                            toBottomHeight={'100%'}
                            dataSource={[
                                {
                                    position: 'right',
                                    type: 'text',
                                    className: 'mb-2',
                                    text: 'Merhaba ürünü ne zaman alabilirsiniz? ',
                                    date: new Date() - 100000,
                                },
                                {
                                    position: 'left',
                                    type: 'text',
                                    text: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit',
                                    date: new Date() - 10000,
                                },
                            ]} />
                    </CardBody>
                    <CardFooter className="bg-light">
                        <Input
                            placeholder="Mesajınız..."
                            className="pl-2"
                            multiline={false}
                            ref="message"
                            value={this.state.message}
                            onChange={this.handleInputChange}
                            onKeyDown={this.handleEnter}
                            rightButtons={
                                <Button
                                    color='white'
                                    className='bg-dark col-2'
                                    onClick={this.handleSendClick}
                                    text={<FontAwesomeIcon icon={faPaperPlane} width="16" />} />
                            }/>
                    </CardFooter>
                </Card>

            </div>
        );
    }
}

export default ChatBox;